'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { signInWithEmailAndPassword, onAuthStateChanged } from 'firebase/auth';
import { auth, firestore } from '@/lib/firebase';
import { doc, setDoc, serverTimestamp } from 'firebase/firestore';
import Link from 'next/link';
import GoogleSignInButton from '@/components/GoogleSignInButton';
import { setSession } from '@/lib/session';
import Image from "next/image";

export default function LoginPage() {
  const router = useRouter();
  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        if (user.emailVerified) {
          router.replace('/dashboard');
        } else {
          router.replace('/confirm-signup?email=' + encodeURIComponent(user.email || ''));
        }
      }
    });

    return () => unsubscribe();
  }, [router]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData(prev => ({
      ...prev,
      [e.target.name]: e.target.value
    }));
    setError(''); // Clear error when user types
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const { user } = await signInWithEmailAndPassword(
        auth,
        formData.email,
        formData.password
      );

      // Set session token
      await setSession();

      // Update last login
      await setDoc(doc(firestore, 'users', user.uid), {
        lastLogin: serverTimestamp()
      }, { merge: true });

      // Clear form data
      setFormData({ email: '', password: '' });

      // Redirect based on email verification using replace
      if (!user.emailVerified) {
        router.replace('/confirm-signup?email=' + encodeURIComponent(formData.email));
      } else {
        router.replace('/dashboard');
      }
    } catch (error: any) {
      setError(getErrorMessage(error.code));
      setLoading(false);
    }
  };

  const getErrorMessage = (code: string) => {
    switch (code) {
      case 'auth/invalid-credential':
        return 'Invalid email or password';
      case 'auth/user-disabled':
        return 'This account has been disabled';
      case 'auth/user-not-found':
        return 'No account found with this email';
      case 'auth/wrong-password':
        return 'Invalid email or password';
      default:
        return 'Failed to sign in. Please try again.';
    }
  };

  return (
    <div className="w-full h-screen bg-gradient-to-br from-indigo-900 via-purple-800 to-pink-700 flex flex-col">
      <main className="w-full h-full flex justify-center items-center p-6">
        <div className="w-full max-w-md backdrop-blur-md bg-white bg-opacity-10 rounded-2xl border border-white border-opacity-20 p-8 shadow-2xl">
          <div className="flex justify-between items-center mb-8">
            <Link href="/" className="flex items-center space-x-2">
              <div className="relative w-10 h-10">
                <Image 
                  src="/paFire_logo.png" 
                  alt="paFire Logo" 
                  fill
                  className="object-contain"
                />
              </div>
              <span className="text-xl font-bold text-white">paFire</span>
            </Link>
            <Link href="/signup" className="text-sm text-gray-200 hover:text-white">
              Need an account?
            </Link>
          </div>

          <h1 className="text-2xl font-bold text-white mb-6">Welcome back</h1>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <label htmlFor="email" className="block text-sm font-medium text-gray-200">
                Email
              </label>
              <input
                type="email"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className="w-full px-4 py-3 bg-white bg-opacity-10 border border-white border-opacity-20 rounded-xl text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-pink-500"
                placeholder="name@company.com"
              />
            </div>
            
            <div className="space-y-2">
              <div className="flex justify-between">
                <label htmlFor="password" className="block text-sm font-medium text-gray-200">
                  Password
                </label>
                <Link href="/forgot-password" className="text-xs text-gray-300 hover:text-white">
                  Forgot password?
                </Link>
              </div>
              <input
                type="password"
                id="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                className="w-full px-4 py-3 bg-white bg-opacity-10 border border-white border-opacity-20 rounded-xl text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-pink-500"
                placeholder="••••••••"
              />
            </div>
            
            <div className="flex items-center">
              <input
                id="remember-me"
                name="remember-me"
                type="checkbox"
                className="h-4 w-4 rounded border-gray-300 text-purple-600 focus:ring-purple-500"
              />
              <label htmlFor="remember-me" className="ml-2 block text-sm text-gray-300">
                Remember me
              </label>
            </div>
            
            <button
              type="submit"
              disabled={loading}
              className="w-full px-6 py-3.5 bg-gradient-to-r from-pink-500 to-purple-600 text-white rounded-xl hover:from-pink-600 hover:to-purple-700 transition-all text-center font-medium shadow-lg"
            >
              {loading ? (
                <span className="flex items-center justify-center">
                  <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Signing in...
                </span>
              ) : 'Sign in'}
            </button>
          </form>
          
          <div className="mt-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-white border-opacity-20"></div>
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-transparent text-gray-300">Or continue with</span>
              </div>
            </div>
            
            <div className="mt-6 grid grid-cols-2 gap-3">
              <button
                className="w-full flex items-center justify-center px-4 py-2 border border-white border-opacity-20 rounded-lg text-white hover:bg-white hover:bg-opacity-10"
              >
                <svg className="h-5 w-5 mr-2" fill="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                  <path d="M12.0003 2C6.47731 2 2.00031 6.477 2.00031 12C2.00031 16.991 5.65731 21.128 10.4383 21.879V14.89H7.89931V12H10.4383V9.797C10.4383 7.291 11.9323 5.907 14.2153 5.907C15.3103 5.907 16.4543 6.102 16.4543 6.102V8.562H15.1923C13.9503 8.562 13.5623 9.333 13.5623 10.124V12H16.3363L15.8933 14.89H13.5623V21.879C18.3433 21.129 22.0003 16.99 22.0003 12C22.0003 6.477 17.5233 2 12.0003 2Z" />
                </svg>
                Facebook
              </button>
              <button
                className="w-full flex items-center justify-center px-4 py-2 border border-white border-opacity-20 rounded-lg text-white hover:bg-white hover:bg-opacity-10"
              >
                <svg className="h-5 w-5 mr-2" fill="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                  <path d="M12.0003 2C6.47731 2 2.00031 6.477 2.00031 12C2.00031 17.523 6.47731 22 12.0003 22C17.5233 22 22.0003 17.523 22.0003 12C22.0003 6.477 17.5233 2 12.0003 2ZM12.0003 3.9908C15.9773 3.9908 19.2113 7.2248 19.2113 11.2018C19.2113 15.1788 15.9773 18.4128 12.0003 18.4128C8.02331 18.4128 4.78931 15.1788 4.78931 11.2018C4.78931 7.2248 8.02331 3.9908 12.0003 3.9908ZM9.9985 7.7496C9.9985 8.5586 9.3385 9.2186 8.5295 9.2186C7.7205 9.2186 7.0605 8.5586 7.0605 7.7496C7.0605 6.9406 7.7205 6.2806 8.5295 6.2806C9.3385 6.2806 9.9985 6.9406 9.9985 7.7496ZM16.9405 7.7496C16.9405 8.5586 16.2805 9.2186 15.4715 9.2186C14.6625 9.2186 14.0025 8.5586 14.0025 7.7496C14.0025 6.9406 14.6625 6.2806 15.4715 6.2806C16.2805 6.2806 16.9405 6.9406 16.9405 7.7496ZM16.3563 13.4486C15.8903 15.0546 14.0803 16.1486 12.0003 16.1486C9.9203 16.1486 8.1103 15.0546 7.6443 13.4486C7.5473 13.1366 7.7613 12.8196 8.0733 12.7226C8.3853 12.6256 8.7023 12.8396 8.7993 13.1516C9.1313 14.2956 10.4593 15.0256 12.0003 15.0256C13.5413 15.0256 14.8693 14.2956 15.2013 13.1516C15.2983 12.8396 15.6153 12.6256 15.9273 12.7226C16.2393 12.8196 16.4533 13.1366 16.3563 13.4486Z" />
                </svg>
                Google
              </button>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
